* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto",sans-serif;
}

body {
  background-color:#a8dadc;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 500px;
  min-height: 600px;
  background: #f1faee;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #1d3557;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.3;
}

.task-form {
  margin-bottom: 32px;
}

.task-input {
  margin-right: 10px;
  padding: 14px 32px 14px 16px;
  border-radius: 4px;
  border: 2px solid #1d3557;
  outline: none;
  width: 320px;
  background: transparent;
}

.task-input::placeholder {
  color: rgba(0,0,0,0.5);
}

.task-button {
  padding: 16px;
  border: none;
  border-radius: 4px 4px;
  cursor: pointer;
  outline: none;
  background: #1d3557;
  color: #fff;
  text-transform: capitalize;
}

.task-input.edit {
  border: 2px solid #149fff;
}

.task-button.edit {
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
  padding: 16px 22px;
}

.task-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.task-row {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: #457b9d;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}


.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 10px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}